/**
 * EmbeddingGemma Integration for TextGraph
 * Based on proven semantic-galaxy implementation
 * Provides high-quality semantic embeddings with WebGPU/WASM detection
 */

import { 
  env,
  AutoModel,
  AutoTokenizer
} from '@xenova/transformers';

// Singleton cache for the model instance and loading promise
const modelCache = {
  instance: null,
  loadingPromise: null,
};

const MODEL_ID = "onnx-community/embeddinggemma-300m-ONNX";

async function checkWebGPUAvailability() {
  try {
    if (!navigator.gpu) return false;
    return !!(await navigator.gpu.requestAdapter());
  } catch (error) {
    console.error("Error checking WebGPU availability:", error);
    return false;
  }
}

class EmbeddingGemmaManager {
  constructor() {
    this.instance = null;
    this.isLoading = false;
    this.isReady = false;
    this.progress = 0;
    this.status = "Waiting to start...";
    this.error = null;
  }

  async loadModel() {
    if (this.instance) {
      return this.instance;
    }

    if (modelCache.instance) {
      this.instance = modelCache.instance;
      this.isReady = true;
      this.status = "Ready";
      return this.instance;
    }

    if (modelCache.loadingPromise) {
      try {
        const instance = await modelCache.loadingPromise;
        this.instance = instance;
        this.isReady = true;
        this.status = "Ready";
        return instance;
      } catch (error) {
        const errorMessage = error instanceof Error ? error.message : "Failed to load model";
        this.isLoading = false;
        this.error = errorMessage;
        this.status = "Error loading model";
        throw error;
      }
    }

    console.log('🔧 TextGraph: Initializing EmbeddingGemma with semantic-galaxy architecture...');
    
    this.isLoading = true;
    this.error = null;
    this.progress = 0;
    this.status = "Initializing...";

    const loadingPromise = (async () => {
      try {
        const progress_callback = (progress) => {
          if (progress.status === "progress" && progress.file.endsWith(".onnx_data")) {
            const percentage = Math.round((progress.loaded / progress.total) * 100);
            this.progress = percentage;
            this.status = `Loading model... ${percentage}%`;
            console.log(`📦 TextGraph: Loading EmbeddingGemma... ${percentage}%`);
          }
        };

        this.status = "Setting up tokenizer...";
        console.log('📝 TextGraph: Setting up tokenizer...');
        const tokenizer = await AutoTokenizer.from_pretrained(MODEL_ID);

        this.status = "Loading model...";
        console.log('🧠 TextGraph: Loading EmbeddingGemma model...');

        // WebGPU detection and optimization (from semantic-galaxy)
        const isWebGPUAvailable = await checkWebGPUAvailability();
        env.backends.onnx.wasm.proxy = !isWebGPUAvailable;
        const device = isWebGPUAvailable ? "webgpu" : "wasm";
        
        console.log(`🎯 TextGraph: Using device: ${device}`);

        const model = await AutoModel.from_pretrained(MODEL_ID, {
          device,
          dtype: "q4",
          model_file_name: isWebGPUAvailable ? "model_no_gather" : "model",
          progress_callback,
        });

        const instance = { model, tokenizer, device };
        this.instance = instance;
        modelCache.instance = instance;
        modelCache.loadingPromise = null;

        this.isLoading = false;
        this.isReady = true;
        this.progress = 100;
        this.status = "Ready for GAT computation";
        
        console.log('✅ TextGraph: EmbeddingGemma loaded successfully');
        console.log(`📊 TextGraph: Model: ${MODEL_ID}`);
        console.log(`🎯 TextGraph: Device: ${device}`);
        console.log(`⚙️ TextGraph: Embedding dimension: 768D`);
        
        return instance;
      } catch (error) {
        modelCache.loadingPromise = null;
        const errorMessage = error instanceof Error ? error.message : "Failed to load model";
        this.isLoading = false;
        this.error = errorMessage;
        this.status = "An error occurred";
        console.error('❌ TextGraph: EmbeddingGemma loading failed:', errorMessage);
        throw error;
      }
    })();

    modelCache.loadingPromise = loadingPromise;
    return loadingPromise;
  }

  /**
   * Generate embeddings for multiple texts with task-specific prefixes
   * @param {Array<string>} texts - Array of text strings
   * @param {string} taskType - 'query' or 'document'
   * @returns {Promise<Array<Array>>} - Array of 768D embedding vectors
   */
  async generateEmbeddings(texts, taskType = 'document') {
    if (!this.instance) {
      await this.loadModel();
    }

    if (!this.instance) {
      throw new Error('EmbeddingGemma model not initialized');
    }

    const startTime = performance.now();
    console.log(`🔄 TextGraph: Generating ${taskType} embeddings for ${texts.length} texts...`);

    try {
      // Apply task prefixes (from semantic-galaxy approach)
      const prefixedTexts = texts.map(text => {
        return taskType === 'query' ? `search_query: ${text}` : `search_document: ${text}`;
      });

      const inputs = this.instance.tokenizer(prefixedTexts, {
        padding: true,
        truncation: true,
        max_length: 256,
      });

      const { sentence_embedding } = await this.instance.model(inputs);
      const embeddings = sentence_embedding.tolist();
      
      const endTime = performance.now();
      const embeddingTime = endTime - startTime;
      console.log(`✅ TextGraph: Generated ${embeddings.length} embeddings in ${embeddingTime.toFixed(2)}ms`);
      console.log(`⚡ TextGraph: Average time per embedding: ${(embeddingTime / embeddings.length).toFixed(2)}ms`);
      
      return embeddings;
    } catch (error) {
      console.error('❌ TextGraph: Embedding generation failed:', error);
      throw error;
    }
  }

  /**
   * Generate single embedding
   */
  async generateEmbedding(text, taskType = 'document') {
    const embeddings = await this.generateEmbeddings([text], taskType);
    return embeddings[0];
  }

  /**
   * Calculate cosine similarity between two embedding vectors
   */
  calculateCosineSimilarity(embeddingA, embeddingB) {
    if (embeddingA.length !== embeddingB.length) {
      throw new Error('Embedding dimensions must match');
    }

    let dotProduct = 0;
    let magnitudeA = 0;
    let magnitudeB = 0;

    for (let i = 0; i < embeddingA.length; i++) {
      dotProduct += embeddingA[i] * embeddingB[i];
      magnitudeA += embeddingA[i] * embeddingA[i];
      magnitudeB += embeddingB[i] * embeddingB[i];
    }

    magnitudeA = Math.sqrt(magnitudeA);
    magnitudeB = Math.sqrt(magnitudeB);

    if (magnitudeA === 0 || magnitudeB === 0) {
      return 0;
    }

    return dotProduct / (magnitudeA * magnitudeB);
  }

  /**
   * Create attention matrix using EmbeddingGemma cosine similarities
   */
  async createEmbeddingAttentionMatrix(tokens, context = 'document') {
    const taskType = context === 'query' ? 'query' : 'document';
    
    console.log(`🔍 TextGraph: Creating EmbeddingGemma attention matrix for ${tokens.length} tokens`);
    
    // Generate embeddings for all tokens
    const embeddings = await this.generateEmbeddings(tokens, taskType);
    
    // Create attention matrix using cosine similarities
    const attentionMatrix = [];
    let minAttention = 1.0;
    let maxAttention = -1.0;
    
    for (let i = 0; i < tokens.length; i++) {
      const row = [];
      for (let j = 0; j < tokens.length; j++) {
        if (i === j) {
          // Self-attention: set to 0 for educational GAT
          row.push(0);
        } else {
          const similarity = this.calculateCosineSimilarity(embeddings[i], embeddings[j]);
          // Convert similarity from [-1,1] to [0,1] range
          const attention = (similarity + 1) / 2;
          row.push(attention);
          
          if (attention > maxAttention) maxAttention = attention;
          if (attention < minAttention && attention > 0) minAttention = attention;
        }
      }
      attentionMatrix.push(row);
    }
    
    console.log(`📊 TextGraph: EmbeddingGemma attention matrix created (${minAttention.toFixed(3)} - ${maxAttention.toFixed(3)})`);
    
    return {
      queryTokens: tokens,
      attentionMatrix: attentionMatrix,
      minAttention: minAttention,
      maxAttention: maxAttention,
      embeddings: embeddings,
      computationDetails: {
        model: MODEL_ID,
        taskType: taskType,
        dimensions: embeddings[0] ? embeddings[0].length : 768,
        method: 'EmbeddingGemma Cosine Similarity',
        device: this.instance?.device || 'unknown'
      }
    };
  }

  /**
   * Get loading status for UI display
   */
  getStatus() {
    return {
      isLoading: this.isLoading,
      isReady: this.isReady,
      progress: this.progress,
      status: this.status,
      error: this.error,
      modelName: MODEL_ID,
      device: this.instance?.device || 'not_loaded',
      embeddingDimension: 768
    };
  }

  /**
   * Start loading the model automatically (like semantic-galaxy)
   */
  async initialize() {
    if (!this.isReady && !this.isLoading) {
      console.log('🚀 TextGraph: Auto-initializing EmbeddingGemma...');
      try {
        await this.loadModel();
      } catch (error) {
        console.warn('⚠️ TextGraph: Auto-initialization failed, will retry on first use');
      }
    }
  }
}

// Create global instance
const embeddingGemmaManager = new EmbeddingGemmaManager();

// Auto-initialize on module load (like semantic-galaxy)
embeddingGemmaManager.initialize();

// Export for use in TextGraph
window.EmbeddingGemmaManager = embeddingGemmaManager;

export default embeddingGemmaManager;